.sib-close-btn{
  display:inline-flex;
}
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone{
      border:0;
      border-radius:var(--sib-border-radius_md, 0.5rem);
      color:var(--sib-color_content-secondary, #687484);
      padding:0;
    }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone svg{
        height:24px;
        width:24px;
      }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone.sib-btn_size--small{
        border-radius:var(--sib-border-radius_sm, 0.25rem);
      }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone.sib-btn_size--small svg{
          height:16px;
          width:16px;
        }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone.sib-btn_size--small:not([disabled]):not(:active):not(:focus-visible):hover{
          box-shadow:0 0 0 1px var(--sib-color_surface-mid-grey-background, #dee5ed);
        }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone:not([disabled]):hover{
          background:var(--sib-color_surface-mid-grey-background, #dee5ed);
          box-shadow:0 0 0 2px var(--sib-color_surface-mid-grey-background, #dee5ed);
          color:var(--sib-color_primary-hover, #005494);
        }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone:not([disabled]):active{
          background:var(--sib-color_surface-mid-grey-background, #dee5ed);
          box-shadow:0 0 0 0;
          color:var(--sib-color_primary-press, #003761);
        }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone:not([disabled]):focus-visible{
          box-shadow:0 0 0 2px var(--sib-color_primary-graphical, #0092ff);
          outline:none;
        }
  .sib-close-btn.sib-btn.sib-btn_icon-stand-alone:disabled{
        cursor:not-allowed;
        opacity:.5;
      }
