.sib-input{
  font-family:var(--sib-typo_font-family--body,
      "Roboto-Regular",
      "system-ui",
      "-apple-system",
      sans-serif);
  position:relative;
}
  .sib-input .sib-input-label{
    color:var(--sib-color_content-primary, #1f2d3d);
    display:table;
    font-family:var(--sib-typo_font-family--body_bold,
        "Roboto-Bold",
        "system-ui",
        "-apple-system",
        sans-serif);
    font-size:var(--sib-typo_text-md-font-size, 1rem);
    line-height:var(--sib-typo_text-line-height, 1.5rem);
    margin-bottom:0.5rem;
  }
  .sib-input .required{
    color:var(--sib-color_negative-default, #e00000);
    margin-left:0.25rem;
  }
  .sib-input .sib-inputbox[disabled]{
    cursor:not-allowed;
  }
  .sib-input .sib-inputbox[disabled] .input{
      background-color:var(--sib-color_surface-white-background, #fff);
      opacity:.5;
      pointer-events:none;
    }
  .sib-input .sib-inputbox :not(textarea).input[type="text"],
    .sib-input .sib-inputbox :not(textarea).input[type="password"]{
      height:2.5rem;
      padding:0.4375rem;
    }
  .sib-input .sib-inputbox :not(textarea).input[type="search"]{
      height:2.5rem;
      padding:0 2rem;
    }
  .sib-input .sib-inputbox textarea.input[type="text"]{
    padding:0.4375rem;
  }
  .sib-input .sib-inputbox{
    align-items:center;
    background:#fff;
    display:-webkit-inline-flex;
    display:flex;
    max-width:100%;
  }
  .sib-input .sib-inputbox .input{
      background:none;
      border:1px solid var(--sib-color_content-secondary, #687484);
      border-radius:var(--sib-border-radius_md, 0.5rem);
      box-sizing:border-box;
      color:var(--sib-color_content-primary, #1f2d3d);
      display:block;
      font-family:var(--sib-typo_font-family--body,
          "Roboto-Regular",
          "system-ui",
          "-apple-system",
          sans-serif);
      font-size:1rem;
      margin:0;
      outline:0;
      width:100%;
    }
  .sib-input .sib-inputbox .input[readOnly]{
        background-color:var(--sib-color_surface-grey-background, #eff2f7);
        color:var(--sib-color_content-secondary, #687484);
        cursor:default;
      }
  .sib-input .sib-inputbox .input:not([disabled]):not([readOnly]):hover{
        border-color:var(--sib-color_content-primary, #1f2d3d);
      }
  .sib-input .sib-inputbox .input:not([disabled]):not([readOnly]):focus{
          border-color:var(--sib-color_primary-graphical, #0092ff);
          box-shadow:inset 0 0 0 1px var(--sib-color_primary-graphical, #0092ff);
        }
  .sib-input .sib-inputbox .input:not([disabled]):not([readOnly]):focus:not([disabled]):hover{
            border-color:var(--sib-color_primary-graphical, #0092ff);
          }
  .sib-input .sib-inputbox .input::-moz-placeholder{
        color:var(--sib-color_content-secondary, #687484);
      }
  .sib-input .sib-inputbox .input:-ms-input-placeholder{
        color:var(--sib-color_content-secondary, #687484);
      }
  .sib-input .sib-inputbox .input::placeholder{
        color:var(--sib-color_content-secondary, #687484);
      }
  .sib-input .sib-inputbox .input[value=""]+.search-clear{
        display:none;
      }
  .sib-input .sib-inputbox .sib-inputbox-search-icon{
      display:flex;
      margin-left:0.5rem;
      position:absolute;
    }
  .sib-input .sib-inputbox .sib-inputbox-search-icon svg path{
          stroke:var(--sib-color_content-primary, #1f2d3d);
        }
  .sib-input .sib-inputbox.sib-inputbox-search-container{
      position:relative;
      width:100%;
    }
  .sib-input .sib-inputbox [type="search"]{
      -webkit-appearance:textfield;
         -moz-appearance:textfield;
              appearance:textfield;
    }
  .sib-input .sib-inputbox [type="search"]::-webkit-search-cancel-button{
        display:none;
      }
  .sib-input .sib-inputbox [type="search"]::-webkit-search-decoration{
        display:none;
      }
  .sib-input .sib-inputbox__with-action-and-count,
    .sib-input .sib-inputbox__with-action-btn,
    .sib-input .sib-inputbox__with-count{
      display:inline-flex;
      position:relative;
    }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions, .sib-input .sib-inputbox__with-count .sib-inputbox__actions{
        align-items:center;
        display:flex;
        position:absolute;
        right:0;
      }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button, .sib-input .sib-inputbox__with-count .sib-inputbox__actions button{
          border-color:transparent;
          cursor:pointer;
          margin-right:0.25rem;
          padding:0.4375rem;
        }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button:not([disabled]):hover, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button:not([disabled]):hover, .sib-input .sib-inputbox__with-count .sib-inputbox__actions button:not([disabled]):hover{
            background-color:var(--sib-color_surface-grey-background, #eff2f7);
            color:var(--sib-color_content-primary, #1f2d3d);
          }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button.sib-btn--selected:not([disabled]),
          .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button:not([disabled]):active,
          .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button.sib-btn--selected:not([disabled]),
          .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button:not([disabled]):active,
          .sib-input .sib-inputbox__with-count .sib-inputbox__actions button.sib-btn--selected:not([disabled]),
          .sib-input .sib-inputbox__with-count .sib-inputbox__actions button:not([disabled]):active{
            background-color:var(--sib-color_primary-background, #ebf7ff);
            color:var(--sib-color_primary-default, #0071c7);
          }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button.sib-btn--selected:not([disabled]):hover, .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button:not([disabled]):active:hover, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button.sib-btn--selected:not([disabled]):hover, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button:not([disabled]):active:hover, .sib-input .sib-inputbox__with-count .sib-inputbox__actions button.sib-btn--selected:not([disabled]):hover, .sib-input .sib-inputbox__with-count .sib-inputbox__actions button:not([disabled]):active:hover{
              color:var(--sib-color_primary-default, #0071c7);
            }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions button:not([disabled]):focus-visible, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions button:not([disabled]):focus-visible, .sib-input .sib-inputbox__with-count .sib-inputbox__actions button:not([disabled]):focus-visible{
            border:2px solid var(--sib-color_primary-graphical, #0092ff);
            box-shadow:none;
            padding:0.375rem;
          }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions__count, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions__count, .sib-input .sib-inputbox__with-count .sib-inputbox__actions__count{
          color:var(--sib-color_content-secondary, #687484);
          font-size:var(--sib-typo_text-sm-font-size, 0.8125rem);
          margin-right:0.5rem;
          text-align:right;
        }
  .sib-input .sib-inputbox__with-action-and-count .sib-inputbox__actions .sib-tooltip, .sib-input .sib-inputbox__with-action-btn .sib-inputbox__actions .sib-tooltip, .sib-input .sib-inputbox__with-count .sib-inputbox__actions .sib-tooltip{
          white-space:nowrap;
        }
  .sib-input .sib-inputbox__with-action-and-count[disabled] .sib-inputbox__actions, .sib-input .sib-inputbox__with-action-btn[disabled] .sib-inputbox__actions, .sib-input .sib-inputbox__with-count[disabled] .sib-inputbox__actions{
        display:none;
      }
  .sib-input .sib-inputbox.sib-inputbox__with-action-and-count .input[type="text"]{
      padding-right:5.1875rem;
    }
  .sib-input .sib-inputbox.sib-inputbox__with-action-btn .input[type="text"]{
      padding-right:2.625rem;
    }
  .sib-input .sib-inputbox.sib-inputbox__with-count .input[type="text"]{
      padding-right:3.0625rem;
    }
  .sib-input.sib-input-errored .sib-inputbox .input,
      .sib-input.sib-input-errored .sib-inputbox textarea{
        border-color:var(--sib-color_negative-default, #e00000);
      }
  .sib-input.sib-input-errored .sib-inputbox .input:not([disabled]):not([readOnly]):hover, .sib-input.sib-input-errored .sib-inputbox textarea:not([disabled]):not([readOnly]):hover{
          border-color:var(--sib-color_negative-default, #e00000);
        }
  .sib-input .sib-input-specification{
    color:var(--sib-color_content-secondary, #687484);
    display:table;
    font-family:var(--sib-typo_font-family--body,
        "Roboto-Regular",
        "system-ui",
        "-apple-system",
        sans-serif);
    font-size:var(--sib-typo_text-sm-font-size, 0.8125rem);
    line-height:var(--sib-typo_text-line-sm-height, 0.8125rem);
    margin:0;
    margin-top:0.5rem;
  }
  .sib-input .sib-input-specification.error{
      color:var(--sib-color_negative-default, #e00000);
    }
  .sib-input .sib-emoji-picker{
    display:flex;
    left:0;
    margin-top:0.5rem;
    position:absolute;
    right:0;
  }
  .sib-input .sib-emoji-picker .emoji-mart{
      justify-content:flex-end;
      margin-left:auto;
    }
  .sib-input.sib-input-inline-block{
    display:inline-block;
  }

.search-clear{
  position:absolute;
  right:8px;
  top:12px;
}
