.sib-loader{
  display:flex;
  z-index:var(--sib-z-index_loader,1099);
}
  .sib-loader svg{
    -webkit-animation:var(--sib-animation_indicator-spin);
            animation:var(--sib-animation_indicator-spin);
  }

.sib-loader--absolute{
  left:50%;
  position:absolute;
  top:50%;
  transform:translate(-50%, -50%);
}