.sib-btn{
  align-items:center;
  background-color:var(--sib-color_primary-default, #0071c7);
  border:1px solid transparent;
  border-color:var(--sib-color_primary-default, #0071c7);
  border-radius:var(--sib-border-radius_md, 0.5rem);
  box-shadow:inset 0 0 0 0.125rem transparent;
  color:var(--sib-color_surface-white-background, #fff);
  display:flex;
  font-family:var(--sib-typo_font-family--body_medium, 
    "Roboto-Medium",
    "system-ui",
    "-apple-system",
    sans-serif);
  font-size:var(--sib-typo_text-md-font-size, 1rem);
  line-height:var(--sib-typo_text-line-height, 1.5rem);
  max-width:100%;
  padding:0.4375rem 0.9375rem;
  position:relative;
  text-align:center;
  text-decoration:none;
  white-space:nowrap;
}

.sib-btn .ellipsis{
    max-width:20ch;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
  }

.sib-btn .sib-btn--label{
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
  }

.sib-btn.sib-btn_icon-stand-alone{
    background-color:inherit;
    border-color:transparent;
    color:var(--sib-color_content-primary, #1f2d3d);
    padding:0.6875rem;
  }

.sib-btn.sib-btn_icon-stand-alone svg{
      margin-right:0;
      stroke:var(--sib-color_content-primary, #1f2d3d);
    }

.sib-btn.sib-btn_icon-stand-alone:not([disabled]):hover,
    .sib-btn.sib-btn_icon-stand-alone:not([disabled]).dropdown-open{
      background-color:var(--sib-color_primary-background, #ebf7ff);
      border-color:transparent;
      color:var(--sib-color_primary-default, #0071c7);
      cursor:pointer;
    }

.sib-btn.sib-btn_icon-stand-alone:not([disabled]):hover svg, .sib-btn.sib-btn_icon-stand-alone:not([disabled]).dropdown-open svg{
        stroke:var(--sib-color_primary-hover, #005494);
      }

.sib-btn.sib-btn_icon-stand-alone:not([disabled]):active{
      background-color:var(--sib-color_primary-background-dark, #ccebff);
      border-color:transparent;
      color:var(--sib-color_primary-press, #003761);
      cursor:pointer;
    }

.sib-btn.sib-btn_icon-stand-alone:not([disabled]):active svg{
        stroke:var(--sib-color_primary-press, #003761);
      }

.sib-btn.sib-btn_icon-stand-alone:focus-visible{
      box-shadow:0 0 0 0.1rem var(--sib-color_surface-white-background, #fff), 0 0 0 0.2rem var(--sib-color_primary-graphical, #0092ff);
      outline:none;
    }

.sib-btn.sib-btn_icon-stand-alone:disabled{
      cursor:not-allowed;
      opacity:.5;
    }

.sib-btn:not([disabled]){
    outline:none;
  }

.sib-btn:not([disabled]):hover{
      background-color:var(--sib-color_primary-hover, #005494);
      border-color:var(--sib-color_primary-hover, #005494);
      cursor:pointer;
    }

.sib-btn:not([disabled]):active{
      background-color:var(--sib-color_primary-press, #003761);
      border-color:var(--sib-color_primary-press, #003761);
    }

.sib-btn:not([disabled]):focus-visible{
      box-shadow:0 0 0 0.1rem var(--sib-color_surface-white-background, #fff), 0 0 0 0.2rem var(--sib-color_primary-graphical, #0092ff);
      outline:none;
    }

.sib-btn:disabled{
    cursor:not-allowed;
    opacity:.5;
  }

.sib-btn svg{
    margin-right:0.5rem;
    pointer-events:none;
    stroke:var(--sib-color_surface-white-background, #fff);
  }

.sib-btn svg.icon-right{
      margin-left:0.5rem;
      margin-right:0;
    }

.sib-btn_variant--secondary{
    background-color:var(--sib-color_surface-white-background, #fff);
    border-color:var(--sib-color_primary-default, #0071c7);
    color:var(--sib-color_primary-default, #0071c7);
  }

.sib-btn_variant--secondary svg{
      stroke:var(--sib-color_primary-default, #0071c7);
    }

.sib-btn_variant--secondary:not([disabled]):hover{
      background-color:var(--sib-color_primary-background, #ebf7ff);
      border-color:var(--sib-color_primary-hover, #005494);
      color:var(--sib-color_primary-hover, #005494);
      cursor:pointer;
    }

.sib-btn_variant--secondary:not([disabled]):hover svg{
        stroke:var(--sib-color_primary-hover, #005494);
      }

.sib-btn_variant--secondary:not([disabled]):active{
      background-color:var(--sib-color_primary-background-dark, #ccebff);
      border-color:var(--sib-color_primary-press, #003761);
      color:var(--sib-color_primary-press, #003761);
      cursor:pointer;
    }

.sib-btn_variant--secondary:not([disabled]):active svg{
        stroke:var(--sib-color_primary-press, #003761);
      }

.sib-btn_variant--tertiary{
    background-color:inherit;
    border-color:transparent;
    color:var(--sib-color_primary-default, #0071c7);
  }

.sib-btn_variant--tertiary svg{
      stroke:var(--sib-color_primary-default, #0071c7);
    }

.sib-btn_variant--tertiary:not([disabled]):hover,
    .sib-btn_variant--tertiary:not([disabled]).dropdown-open{
      background-color:var(--sib-color_primary-background, #ebf7ff);
      border-color:transparent;
      color:var(--sib-color_primary-hover, #005494);
      cursor:pointer;
    }

.sib-btn_variant--tertiary:not([disabled]):hover svg, .sib-btn_variant--tertiary:not([disabled]).dropdown-open svg{
        stroke:var(--sib-color_primary-hover, #005494);
      }

.sib-btn_variant--tertiary:not([disabled]):active{
      background-color:var(--sib-color_primary-background-dark, #ccebff);
      border-color:transparent;
      color:var(--sib-color_primary-press, #003761);
      cursor:pointer;
    }

.sib-btn_variant--tertiary:not([disabled]):active svg{
        stroke:var(--sib-color_primary-press, #003761);
      }

.sib-btn_variant--destructive{
    background-color:var(--sib-color_negative-default, #e00000);
    border-color:var(--sib-color_negative-default, #e00000);
    color:var(--sib-color_content-white, #fff);
  }

.sib-btn_variant--destructive svg{
      stroke:var(--sib-color_content-white, #fff);
    }

.sib-btn_variant--destructive:not([disabled]):hover{
      background-color:var(--sib-color_negative-hover, #ad0000);
      border-color:var(--sib-color_negative-hover, #ad0000);
      cursor:pointer;
    }

.sib-btn_variant--destructive:not([disabled]):active{
      background-color:var(--sib-color_negative-press, #7a0000);
      border-color:var(--sib-color_negative-press, #7a0000);
      cursor:pointer;
    }

.sib-btn--loader{
    opacity:.5;
  }

.sib-btn--loader .sib-loader svg{
        margin-right:0;
      }

.sib-btn--loader>*:not(.sib-loader){
    opacity:0;
  }

.sib-btn--full-width{
    justify-content:center;
    width:100%;
  }

