.sib-form{
  display:flex;
  list-style:none;
  margin:0;
  padding:0;
}
  .sib-form.sib-form-row{
    flex-direction:row;
  }
  .sib-form.sib-form-row .sib-form--item:not(:first-child){
        margin-left:1rem;
      }
  .sib-form.sib-form-column{
    flex-direction:column;
  }
  .sib-form.sib-form-column .sib-form--item:not(:first-child){
        margin-top:1rem;
      }
